<template>
    <div class="coupon-style-two" :class="stateFunc(data.isUse,data.isUseTime).background">
        <div class="top">
            <p class="head">
                <span class="price">{{ keepDisNum(data.discount) }}</span>
            </p>
            <span class="type">{{ $L('折扣券') }}</span>
        </div>
        <div class="bottom">
            <div class="condtion" v-if="parseFloat(data.couponsWherePrice)"><span>{{ $L('使用条件') }}:</span>{{ data.couponsWherePrice}}</div>
            <div class="condtion" v-else-if="!parseFloat(data.couponsWherePrice)"><span>{{ $L('使用条件') }}:</span>{{ $L('全部商品可用') }}</div>
            <div class="time">
                <span>{{ data.startTime }}</span>&nbsp;-
                <span>{{ data.endTime }}</span>
            </div>
            <div class="coupon-btn canUse" v-if="data.isUseTime === 1 && data.isUse === 0">
                <el-button class="btn" type="primary" plain size="small" @click="goHome">{{ $L('立即使用') }}</el-button>
            </div>
            <div class="coupon-btn canUse" v-else-if="data.isUse === 1">
                <el-button class="btn" type="primary" plain size="small" disabled>{{ $L('已使用') }}</el-button>
            </div>
            <div class="coupon-btn canUse" v-else-if="data.isUseTime === 2 && data.isUse === 0">
                <el-button class="btn" type="primary" plain size="small" disabled>{{ $L('不可用') }}</el-button>
            </div>
            <div class="coupon-btn canUse" v-else>
                <el-button class="btn" type="primary" plain size="small" disabled>{{ $L('已过期') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "couponStyleTwo",
        props:{
            data:{
                type:Object,
                default:{}
            },
        },
        methods : {
            // 乘以10
            keepDisNum(val){
                let num = Number(val);
                return (num*10).toFixed(1);
            },
            stateFunc(isuse,time) {
                let x;
                if(isuse === 0 && time === 1){
                    x = {background:'background'}
                } else {
                    x = {background: 'backgroundgrey'};
                }
                return x;
            },
            goHome(){
                this.$gotoWebHome();
            },
        }
    }
</script>

<style lang="scss" scoped>
    .coupon-style-two{
        color: #fff;
        background-size: 100% 100% !important;
        height: 120px;
        &.background{
            background: url("../../../assets/img/distributor/coupon-bg-mobile.png");
        }
        &.backgroundgrey{
            background: url("../../../assets/img/distributor/coupon-bggrey-mobile.png");
        }
        .top{
            width: 35%;
            display: inline-block;
            vertical-align: top;
            .head{
                width: 100%;
                height: 75px;
                line-height: 75px;
                .unit{
                    font-size: 26px;
                    vertical-align: top;
                }
                .price{
                    font-size: 50px;
                }
            }

            .type{
                display: inline-block;
                font-size: 16px;
                padding-left: 5px;
            }
        }
        .bottom{
            color: #333;
            width: 65%;
            display: inline-block;
            vertical-align: top;
            height: 120px;
            padding: 10px 10px 20px;
            .condtion{
                text-align: left;
                padding-bottom: 5px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .time{
                text-align: left;
            }
            .coupon-btn {
                margin-top: 3px;
                text-align: left;
                .btn{
                    width: 100px;
                }
            }
        }
    }
</style>